export default {
	name: 'activity-mini-card',
	computed: {
		backgroundImage: function() {
			if (this.activity.img_url) {
				return {
					'background-image': 'url(' + this.activity.img_url + ')',
				}
			} else {
				return {}
			}
		}
	},
	props: {
		activity: {
			type: Object
		}
	},
	methods: {}
}
